<template>
    <div class="orderPay">
        <van-nav-bar title="提交订单" left-text="返回" :fixed="true" left-arrow @click-left="$router.go(-1)"/>

        <div class="box">

            <div class="site" @click="isShow = true" v-if="userAddress">
                <div class="info">
                    <span class="name">{{ userAddress.name }}</span>
                    <span>{{ userAddress.tel }}</span>
                </div>
                <p>{{ userAddress.address }}</p>
            </div>
            <div class="addsite" @click="isShow = true" v-else>
                <i class="fa fa-plus" aria-hidden="true"></i>
                <p>添加收货地址</p>
            </div>

            <div class="orderPay-content">

                <div class="orderPay-item">

                    <div class="orderPay-goods">
                        <div class="goods" v-for="(item, index) in orderData" :key="index">
                            <div class="goods-img">
                                <img class="auto-img" :src="item.small_img" alt="">
                            </div>

                            <div class="goods-content">
                                <div class="goods-text">
                                    <div class="goods-title">
                                        <span class="name">{{ item.name }}</span>
                                        <div class="price">￥{{ item.price }}</div>
                                    </div>
                                    <div class="enname">{{ item.enname }}</div>
                                </div>
                                
                                <div class="goods-rule">
                                    <span class="rule">{{ item.rule }}</span>
                                    <span class="count">×{{ item.count }}</span>
                                </div>
                            </div>
                        </div>

                        <div class="total">
                            <span class="total-count">共计 {{ count }} 件商品 合计：</span>
                            <span class="total-pic">￥{{ total }}</span>
                        </div>
                    </div>

                </div>

            </div>

        </div>

        <van-submit-bar :price="total * 100" button-text="立即支付" @submit="payNow" />

        <van-popup v-model="isShow" round position="bottom">
            <van-address-list
                v-model="aid"
                :list="addressData"
                default-tag-text="默认"
                @add="$router.push({ name: 'NewAddress' });"
                @select="selectAddress"
            />
        </van-popup>
    </div>
</template>

<script>
    import { Toast } from "vant";

    export default {

        data() {
            return {
                isShow: false,

                sids: null,

                addressData: [],

                userAddress: null,

                aid: null,

                orderData: null,
                // 总数量
                count: 0,
                // 总价格
                total: 0,
            };
        },

        methods: {
            // 获取地址数据
            getAddressData() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    // 延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/findAddress",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 20000) {

                        res.data.result.forEach(item => {
                            let currentAddress = {
                                id: item.aid,
                                name: item.name,
                                tel: item.tel,
                                address: item.province + item.city + item.county + item.addressDetail,
                                isDefault: Boolean(item.isDefault),
                            }

                            if(item.isDefault) {
                                this.aid = item.aid;

                                this.userAddress = {
                                    // id: item.aid,
                                    name: item.name,
                                    tel: item.tel,
                                    address: item.province + item.city + item.county + item.addressDetail,
                                    // isDefault: Boolean(item.isDefault),
                                };
                            }

                            this.addressData.push(currentAddress);
                        });

                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 选择地址
            selectAddress(item) {
                this.isShow = false;

                this.userAddress = {
                    // id: item.aid,
                    name: item.name,
                    tel: item.tel,
                    address: item.address,
                    // isDefault: Boolean(item.isDefault),
                }
            },

            // 获取提交订单数据
            getOrderData() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    // 延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/commitShopcart",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                        sids: JSON.stringify(this.sids),
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 50000) {
                        res.data.result.forEach(item => {
                            this.count += item.count;
                            this.total += item.count * item.price;
                        });

                        this.orderData = res.data.result;
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 立即支付
            payNow() {
                if(!this.userAddress) {
                    Toast.fail("请添加收货地址");
                    return;
                }

                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    // 延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/pay",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        sids: JSON.stringify(this.sids),
                        phone: this.userAddress.tel,
                        address: this.userAddress.address,
                        receiver: this.userAddress.name,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 60000) {
                        this.$router.push({ name: "Order" });
                    }

                }).catch(err => {
                     // 关闭加载提示
                    Toast.clear();


                });
            }

        },

        created() {
            this.sids = this.$route.query.sids.split("-");

            // 获取地址数据
            this.getAddressData();

            // 获取提交订单数据
            this.getOrderData();

        },

    }
</script>

<style lang="less" scoped>
    .orderPay{
        color: #646566;
    }

    .van-nav-bar{
        height: 45px;
    }

    .box{
        height: calc(100vh - 45px * 2);
        margin-top: 45px;
        padding: 10px;
        box-sizing: border-box;
        overflow-y: auto;

        .site{
            padding: 10px;
            background-color: #fff;
            border-radius: 10px;

            .info{
                span{
                    font-size: 14px;

                    &.name{
                        margin-right: 10px;
                        color: #000;
                        font-weight: bold;
                    }
                }
            }

            p{
                margin-top: 5px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
        }

        .addsite{
            display: flex;
            height: 45px;
            background-color: #fff;
            border-radius: 10px;
            font-size: 14px;
            justify-content: center;
            align-items: center;

            .fa-plus{
                margin: 0 10px;
                color: #1989FA;
            }

        }

        .orderPay-content{
            
            .orderPay-item{
                margin-top: 10px;
                padding: 10px;
                background-color: #fff;
                border-radius: 10px;

                .orderPay-goods{
                    border-top: 1px solid #F3F3F3;
                    // border-bottom: 1px solid #F3F3F3;

                    .goods{
                        display: flex;
                        padding-top: 10px;
    
                        .goods-img{
                            width: 70px;
                            height: 70px;
                        }

                        .goods-content{
                            display: flex;
                            flex: 1;
                            margin-left: 10px;
                            flex-direction: column;
                            justify-content: space-between;

                            .goods-title, .goods-rule{
                                display: flex;
                                justify-content: space-between;
                            }

                            .name{
                                font-size: 16px;
                            }

                            .enname{
                                margin-top: 2px;
                                color: #A8A8A8;
                            }

                            .rule, .count{
                                color: #A8A8A8;
                            }

                        }

                    }

                    .total{
                        margin-top: 10px;
                        text-align: right;
                        line-height: 30px;

                        .total-count{
                            color: #A8A8A8;
                        }

                        .total-pic{
                            font-size: 16px;
                            font-weight: bold;
                        }
                    }
                    
                }
            }
        }
    }

    .van-submit-bar{
        
        .van-submit-bar__bar{
            height: 45px;
            justify-content: space-between;
        }

        .van-submit-bar__price{
            color: #1989FA;
        }

        .van-submit-bar__button{
            height: 35px;
        }

        .van-submit-bar__button--danger{
            background: linear-gradient(to right, #6fb7ff, #1989FA);
        }

    }

    .van-icon-edit{
        display: none;
    }
</style>